import React, { useEffect, useState } from "react";
import "./style.scss";
import QRCode from "qrcode.react";
import "@resconet/jsbridge";

const MobileCRM = window.MobileCRM;

function App() {
  const [QRString, setQRString] = useState("");
  useEffect(() => {
    MobileCRM.UI.EntityForm.requestObject(
      (entityForm) => {
        setQRString(`rescohouston://open?jsbridge;${entityForm.entity.entityName};${entityForm.entity.id}`);
      },
      (error) => {
        console.log("Problem with loading data");
      }
    );
  }, []);

  return (
    <div className="App">
      <QRCode value={QRString} bgColor="#EFF2F8" size={256}/>
    </div>
  );
}

export default App;
